@import './vars.scss';

.content {
  margin: auto;
  padding-bottom: 32px;

  > h1 {
    text-align: center;
    margin-top: 16px;
    margin-bottom: 8px;
    color: $color-blue-1;
  }

  h4 {
    color: $color-header;

    @media (max-width: $max-mobile-width) {
      margin-right: 8px;
      margin-left: 8px;
    }
  }

  li {
    margin: 8px 0;
  }
}

.column {
  @media (min-width: $min-desktop-width) {
    max-width: $column-width;
  }
}

h3 {
  font-size: 32px;
  color: $color-gray-1;
  margin: 0;
  margin-bottom: 4px;
}

.page-header-image {
  img {
    width: 100%;
    height: 100%;
  }

  width: 75%;
  margin: auto;

  @media (max-width: $max-mobile-width) {
    width: 100%;
  }
}

@media (max-width: $max-mobile-width) {
  h3 {
    font-size: 24px;
  }
}
